
@import 'https://fonts.googleapis.com/css?family=Ubuntu+Mono';/* COLORS *//* SCREEN SIZES *//* media queries *//* SPACING *//* RADIUS *//* LOGO-SIZE *//* FONTS */html { 
  -webkit-font-smoothing: antialiased;
  font-family: 'Helvetica';
}body {
  background: url(../img/background.png) no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  margin: 0;
}.landing {
  margin: 0 auto;
  margin-top: 90px;
  font-weight: 700;
}.landing .subtitle {
    font-size: 22px;
    color: #5B5B5B;
    margin-bottom: 48px;
  }@media (max-width: 480px) {
  .landing {
    max-width: 90%;
  }
}@media (min-width: 481px) {
  .landing {
    max-width: 80%;
  }
}@media (max-width: 960px) {
    .landing .subtitle {
      font-size: 16px;
    }
    .landing .title {
      font-size: 30px;
    }
}@media (min-width: 961px) {
  .landing {
    max-width: 900px;
  }
}



