.modalContent {
  padding: 16px 16px 32px 16px;
  height: 400px;
  min-width: 350px;
}
textarea {
  resize: none;
  overflow: auto;
}
.form {
  margin: 20px;
}
.input {
  display: block;
  width: 90%;
  margin: 0;
  margin-bottom: 24px;
  border: none;
  border-bottom: 2px solid #4468FB;
  font-size: 16px;
  padding: 0.5rem 0;
  background-repeat: no-repeat;
  color: #5B5B5B;
  font-weight: 500;
}
.input:focus, .input:valid {
    -webkit-box-shadow: none;
            box-shadow: none;
    outline: none;
    background-position: 0 0;
  }
.input[disabled] {
    border-bottom: 1px dashed rgba(0,0,0, 0.12);
    background-color: transparent;
  }
.input::-webkit-input-placeholder {
    color: #4468FB;
    font-size: 16px;
  }
.submit {
  float: right;
  margin-top: 32px;
  display: inline-block;
}
.submit .submitButton {
    color: #FFFFFF !important;
    background-color: #4468FB !important;
    border: none !important;
    cursor: pointer;
  }
.submit .submitButton:hover {
      background-color: #4468CB !important;
    }
.submit .closeButton {
    color: #FFFFFF !important;
    background-color: #5B5B5B !important;
    border: none !important;
    cursor: pointer; 
  }



