.modalContent {
  padding: 16px 16px 32px 16px;
  height: 400px;
  min-width: 350px;
}
.content .terminalHeader {
    height: 30px;
  }
.content .redDot {
    border-radius:8px;
    border:1px solid #F96261;
    background-color: #F96261;
    width:8px;
    height:8px;
    display: inline-block;
    margin-top: 8px;
    margin-left: 8px;
  }
.content .greenDot {
    border-radius:8px;
    border:1px solid #3DC951;
    background-color: #3DC951;
    width:8px;
    height:8px;
    display: inline-block;
    margin-top: 8px;
    margin-left: 4px;
  }
.content .yellowDot {
    border-radius:8px;
    border:1px solid #FBBB4C;
    background-color: #FBBB4C;
    width:8px;
    height:8px;
    display: inline-block;
    margin-top: 8px;
    margin-left: 4px;
  }
.content .dockerContent {
    margin-top: 56px;
    height: 300px;
    width: 100%;
    background: #232424;
    border-radius: 3px;
  }
.content .dockerContent .cmd {
      padding: 20px;
      color: #FFFFFF;
      font-family: 'Ubuntu Mono';
      font-size: 16px;
      font-weight: 600;
    }
.content .dockerContent .cmd a {
        color: #FFFFFF;
        text-decoration: underline;
      }
.content .contactText {
    margin-top: 40px;
    color: #5B5B5B;
    font-weight: 600;
    font-size: 22px;
  }
.content .contactText a {
      text-decoration: none;
    }
.close {
  float: right;
  margin-top: 32px;
  display: inline-block;
}
.close .closeButton {
    color: #FFFFFF !important;
    background-color: #5B5B5B !important;
    border: none !important;
    cursor: pointer;
  }



